export const dateToDiffStr = (now, prev) => {
  const seconds = 1;
  const minute = seconds * 60;
  const hour = minute * 60;
  const day = hour * 24;
  const diff = Math.trunc((now.getTime() - prev.getTime()) / 1000);
  let diffStr = "";
  if (diff < seconds) {
    diffStr = " ";
  } else if (diff < minute) {
    diffStr = diff + " ";
  } else if (diff < hour) {
    diffStr = Math.trunc(diff / minute) + " ";
  } else if (diff < day) {
    diffStr = Math.trunc(diff / hour) + " ";
  } else if (diff < day * 15) {
    diffStr = Math.trunc(diff / day) + " ";
  } else {
    diffStr = prev.format("yyyy MM dd");
  }

  return diffStr;
};
